body {
  background: #fff !important; }

.category .listnav {
  position: fixed;
  top: 0;
  bottom: 1.5rem;
  left: 0;
  width: 2.2rem;
  border-right: 1px solid #cdcdcd; }
  .category .listnav ul {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll; }
    .category .listnav ul li {
      font-size: 0.4rem;
      height: 1.2rem;
      line-height: 1.2rem;
      overflow: hidden;
      text-align: center; }
      .category .listnav ul li a {
        display: block; }
    .category .listnav ul .active a {
      color: #88c322;
      font-size: 0.5rem; }
  .category .listnav ul::-webkit-scrollbar {
    display: none; }
.category .listwarp {
  position: absolute;
  left: 2.2rem;
  right: 0;
  top: 0;
  bottom: 1.5rem;
  padding: 0.3rem .32rem;
  overflow: auto; }



